const axios = require('axios')
const qs = require('qs')

import getCookie from '@/service/getCookie'

const axiosFormSubmit = async (url, data = {}, debugMsg = '', option) => {
  if(option == null){   
    let SetOption = {
      'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8;',
      'x-api-key': getCookie('api-keys') ? JSON.parse(getCookie('api-keys'))[0] : '',
      'x-api-host': 'mycrm',
    }
    option = SetOption
  }
  
  
  // 判斷如果 url 有 http 就不加動態網域
  let API_BASEURL = ''
  if(url.indexOf('http') > -1){
    API_BASEURL = ''
  }else{
    API_BASEURL = 'http://localhost:8081/MXWeb'
    if(window.location.href.indexOf('localhost') !== -1){
      API_BASEURL = 'http://localhost:8081/MXWeb'
    }else if(window.location.href.indexOf('tmycrm') !== -1){
      API_BASEURL = 'https://tcrmapi.cathaylife.com.tw/MXWeb'
    }else if(window.location.href.indexOf('smycrm') !== -1){
      API_BASEURL = 'https://scrmapi.cathaylife.com.tw/MXWeb'
    }else{
      // API_BASEURL = 'http://10.87.23.58:8081/MXWeb'
      API_BASEURL = `http://${window.location.href.match(/\d*\.\d*\.\d*\.\d*/g)}:8081/MXWeb`
    }
  }

  const apiUrl = `${API_BASEURL}${url}`
  console.log(`Sent[${debugMsg}],url:${apiUrl}`)
  console.log(data)
  console.log(qs.stringify(data))  
  
  return new Promise((resolve, reject) => {
    axios({
      method: 'post',
      // url: `${API_PROXY}${API_BASEURL}${url}`,
      url: apiUrl,
      data: qs.stringify(data),
      // headers: {
      //   'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8;',
      //   'X-Requested-With': 'XMLHttpRequest',
      // },
      headers: option,
      timeout: 60000,
      responseType: 'json',
      withCredentials: true,
    })
    .then(response => {
      console.log(`Got[${debugMsg}]Res:${apiUrl}`)
      console.log(response)
      if (response.status === 200) {
        resolve(response.data);
      } else {
        console.error(response);
        let errorData = {
          header:{
            message: "連線逾時 請重新操作",
            status: "400",
            userDeviceIp: "",
            userId: "",
          },
          body:{},
        }
        resolve(errorData);
      }
    })
    .catch(error => {
      console.error(error);
      let errorData = {
        header:{
          message: "連線逾時 請重新操作",
          status: "400",
          userDeviceIp: "",
          userId: "",
        },
        body:{},
      }
      resolve(errorData);
      // reject(error);
    })
  })
}

export default axiosFormSubmit;