<template>
  <main class="container-fluid">
    <div class="segment">
      <h1 class="mb-0">登入</h1>
      <h4 class="mt-0">CRM 客戶關係管理</h4>
      <form class="form">
        <div class="form-group row">
          <label for="account" class="col-2 col-label-form text-right">帳號</label>
          <div class="col-9">
            <input type="text"
            name="account"
            id="account"
            class="form-control"
            :class="{'is-invalid': errors.has('account')}"
            placeholder="請輸入帳號"
            v-model="form.account"
            v-validate="'required'" />
          </div>
        </div>
        <div class="form-group row">
          <label for="password" class="col-2 col-label-form text-right">密碼</label>
          <div class="col-9">
            <input
            type="password"
            name="password"
            id="password"
            class="form-control"
            :class="{'is-invalid': errors.has('password')}"
            v-model="form.password"
            placeholder="請輸入密碼"
            v-validate="'required'"/>
          </div>
        </div>
        <div class="form-group">
          <!-- 由後端回傳錯誤內容 -->
          <div class="error-block" :style="{
            opacity: submitError || errors ? 1 : 0
          }">
            <span class="error">
              您輸入的帳號或密碼有誤，請再次確認。
            </span>
          </div>
        </div>
        <div class="action">
          <button
          @click.stop.prevent="doSubmit()"
          type="submit"
          class="btn btn-primary btn-block"
          :disabled="!isFormValid">
            登入
          </button>
          <a href="#" class="link">忘記密碼 ❯</a>
        </div>
      </form>
    </div>
  </main>
</template>

<script>
export default {
  name: 'Login',
  data () {
    return {
      submitError: true,
      form: {
        account: '',
        password: ''
      }
    }
  },
  computed: {
    isFormValid() {
      let valid = true
      // 判斷欄位是否為空
      return Object.keys(this.fields).reduce(
        (bool, key) => {
          if(!this.fields[key].valid) valid = this.fields[key].valid
          return valid
        }, valid);
    }
  },
  methods: {
    doSubmit () {
      this.$validator.validateAll().then(result => {
        if (!result) return false;

        // login api
        this.$router.push('/')
      });
    }
  }
}
</script>
