export const commonMixin = {
  data() {
    return {
      isChecking: false,
      currentTab: 0,
      isScrolling: false
    }
  },
  methods: {
    checkboxChange ($event, name) {
      if (this.isChecking) return

      let checked = $event.target.checked
      let isAll = $event.target.value === 'all'
      let item = this.form[name]
      this.isChecking = true
      
      if(isAll && checked) {
        this.$el.querySelectorAll(`[name='${name}[]']`).forEach(el => {
          if(item.indexOf(el.value) === -1) {
            item.push(el.value)
          }
        })
      } else if(!isAll && !checked) {
        this.$el.querySelectorAll(`[name='${name}[]']`).forEach(el => {
          if(el.value==='all') el.checked = false
        })

        this.form[name] = item.reduce((ary, value) => {
          if (value !== 'all') {
            ary.push(value)
          }
          return ary
        }, [])
      }
      this.isChecking = false
    },
    panelOpen(name) {
      this.setPageLock(true)
      document.querySelector('html').classList.add('page-lock')
      this[name] = true;
    },
    panelClose(name) {
      var open = document.querySelectorAll('.modal.is-open'),
          elem = open[open.length - 1];
      
      elem.className += ' is-out';
      setTimeout(() => {
        elem.classList.remove('is-out');
        if(open.length==1) {
          this.setPageLock(false)
          document.querySelector('html').classList.remove('page-lock')
        }
        this[name] = false;
      }, 300);
    }
  },
  mounted () {
    window.$('[data-toggle="tab"]').on('shown.bs.tab', (e) => {
      let li = window.$(e.target).closest('.nav-item')
      this.currentTab = window.$('ul[role="tablist"] > li').index(li)
        
      if (li.className == "dropdown-item") {
        let link = li.querySelector('a')
        window.$(e.target).attr('aria-selected', false)
        window.$(e.target).removeClass('active show')
        window.$(link).addClass('active show')
      }
    });
  }
}