<template>
  <div class="login-panel">
    <div class="row">
      <div class="col-12">
        <div id="cathay-logo">
          <img width="110"
          :src="`${BaseUrl}assets/common/cathay_logo.png`"
          :srcset="`${BaseUrl}assets/common/cathay_logo@2x.png`"/>
        </div>
      </div>
      <div class="col-6 d-none d-md-flex">
        <img width="243" class="login-img"
        :src="`${BaseUrl}assets/login/img_login.png`"
        :srcset="`${BaseUrl}assets/login/img_login@2x.png`">
      </div>
      <div class="col-12 col-md-6">
        <div class="title mobile">
          <h1 class="mb-0">登入</h1>
          <h4 class="mt-0">CRM 客戶關係管理</h4>
        </div>
        <h3 class="title desktop">登入 CRM 客戶關係管理</h3>
        <form class="form">
          <div class="form-group row">
            <label for="account" class="col-12 col-md-2 col-label-form">帳號</label>
            <div class="col-12 col-md-10">
              <input type="text"
              name="account"
              id="account"
              class="form-control"
              :class="{'is-invalid': errors.has('account')}"
              placeholder="請輸入帳號"
              v-model="form.account"
              v-validate="'required'" />
            </div>
          </div>
          <div class="form-group row">
            <label for="password" class="col-12 col-md-2 col-label-form">密碼</label>
            <div class="col-12 col-md-10">
              <input type="password"
              name="password"
              id="password"
              class="form-control"
              :class="{'is-invalid': errors.has('password')}"
              v-model="form.password"
              placeholder="請輸入密碼"
              v-validate="'required'"/>
            </div>
          </div>
          <div class="form-group">
            <div class="error-block" :style="{
              opacity: submitError ? 1 : 0
            }">
              <span class="error">
                您輸入的帳號或密碼有誤，或是無此帳號，請再次確認。
              </span>
            </div>
          </div>
          
          <div class="action pt-4">
            <button
            :class="{'section-loading': login_loading}"
            @click.stop.prevent="doSubmit()"
            type="submit"
            class="btn btn-primary btn-block"
            :disabled="!isFormValid || login_loading">
              登入
            </button>
            <a href="#" class="link">忘記密碼 ❯</a>
          </div>
        </form>
      </div>
    </div>

    <form class="d-none" name="SAML" method="post" :action="SAMLForm.formAction">
      <input type="text" name="RelayState" v-model="SAMLForm.RelayState">
      <input type="text" name="SAMLRequest" v-model="SAMLForm.SAMLRequest">
      <input id="SAMLForm" type="submit" value="Submit">
    </form>

  </div>
</template>

<script>
import {mapGetters, mapActions} from 'vuex'
import {getUserSSO} from '../../service'
import axiosFormSubmit from '../../service/axiosFormSubmit.js'
import getCookie from '@/service/getCookie'


export default {
  name: 'Login',
  data () {
    return {
      login_loading: false,
      submitError: false,
      form: {
        account: '',
        password: ''
      },
      samlStatus: '',
      SAMLForm:{
        formAction: '',
        RelayState: '',
        SAMLRequest: '',
      },
    }
  },
  computed: {
    ...mapGetters(['BaseUrl', 'SliceMode']),
    isFormValid() {
      return ( this.form.password.length > 0 ) && ( this.form.account.length > 0)
      // let valid = true
      // // 判斷欄位是否為空
      // return Object.keys(this.fields).reduce(
      //   (bool, key) => {
      //     if(!this.fields[key].valid) valid = this.fields[key].valid
      //     return valid
      //   }, valid);
    }

  },
  methods: {
    ...mapActions(['updateUserToken','updateQueryKeyword','updateSamlStatus']),
    deleteCookie(){
      var cookies = document.cookie.split(";");
      for (var i = 0; i < cookies.length; i++) {
          var cookie = cookies[i];
          var eqPos = cookie.indexOf("=");
          var name = eqPos > -1 ? cookie.substr(0, eqPos) : cookie;
          document.cookie = name + "=;expires=Thu, 01 Jan 1970 00:00:00 GMT;path=/";
      }
    },
    checkSAML(){
      let url = '/servlet/HttpDispatcher/MXAuth/isLoggedIn'
      axiosFormSubmit(url,{},'引導登入頁').then(result => {
        this.login_loading = true
        if(result && result.returnMsg){
          const SAMLStatus = getCookie('saml-status')
          this.updateSamlStatus(getCookie('saml-status'))
          if( SAMLStatus && SAMLStatus === 'on'){  // 如果 SAML 有開啟 formSubmit 出去導頁
            this.SAMLForm.formAction = result.detail.ssoUrl
            this.SAMLForm.RelayState = result.detail.RelayState
            this.SAMLForm.SAMLRequest = result.detail.SAMLRequest
            const submitForm = document.querySelector('#SAMLForm')
            setTimeout(() => {
              submitForm.click()
            }, 200);
            this.login_loading = false
          }else if( SAMLStatus && SAMLStatus === 'off'){ //  如果 SAML 關閉
            console.log('SAML 關閉');
            this.login_loading = false
          }else{  // 回傳的 detail 裡面沒有 ssoUrl & loginUrl 值
            console.log('API 錯誤');
            this.login_loading = false
          }
        }else{
          this.$toast.info({title: '登入錯誤'})
          this.login_loading = false
        }
      })
    },
    doSubmit () {
      // form submit 登入
      this.login_loading = true
      this.$validator.validateAll().then(result => {
        if (!result) return false;
        let url = '/servlet/HttpDispatcher/MXAuth/login'
        let data = {
          userID: this.form.account.toUpperCase(),
          passWD: this.form.password,
        }
        axiosFormSubmit(url,data,'使用者登入').then(result => {
          let userToken = decodeURI(getCookie('api-keys'))
          if(userToken){
            getUserSSO().then(result => {
              if(result.body && result.body.userInfo){
                let user = result.body.userInfo
                user.workDate = result.body.workDate
                this.$store.dispatch({
                  type: 'updateUserInfo',
                  data: user
                })
                this.updateUserToken(JSON.parse(userToken)[0])
                this.login_loading = false
                this.$router.push({path:'/'})
              } else {
                this.login_loading = false
                this.submitError = true
                console.log('登入失敗');
              }
            })
          }else{
            this.login_loading = false
            this.submitError = true
            console.log('未接收到 Token 或是 API key 格式不對');
          }
        }) 
      
      })

    }
  },
  mounted() {
    this.updateQueryKeyword('')
    this.checkSAML()
  },
}
</script>
